import axios from "axios";

const baseURL = 'sidehustlers-env-2.eba-ahjkhez9.us-east-2.elasticbeanstalk.com'


function RefreshService(userName, userPassword) {
    return axios({
        method: 'post',
        url: 'https://' + baseURL + ':5432/refreshToken',
        headers: {},
        data: {
            userName: userName,
            password: userPassword
        }
    })
}

export default RefreshService