import axios from "axios";

function LoginService(userName, userPassword) {
    const baseURL = 'sidehustlers-env-2.eba-ahjkhez9.us-east-2.elasticbeanstalk.com'
    return axios({
        method: 'post',
        url: 'https://' + baseURL + ':5432/authenticate',
        headers: {},
        data: {
            userName: userName,
            password: userPassword
        }
    })
}

export default LoginService