import axios from "axios";

function ProfileService() {


}

const baseURL = 'sidehustlers-env-2.eba-ahjkhez9.us-east-2.elasticbeanstalk.com'

const getAllPosts = async (token, user) => {

    return axios({
        method: 'post',
        url: 'https://' + baseURL + ':5432/posts/getAllPostsByUser',
        headers: {
            Authorization: 'Bearer ' + token
        },
        data: {
            userName: user
        }
    })
}

const deletePost = (token, postId) => {
    return axios({
        method: 'post',
        url: 'http://' + baseURL + ':5432/posts/deletePostById',
        headers: {
            Authorization: 'Bearer ' + token
        },
        data: {
            postId: postId,
        }
    })
}

const editPost = (token, postId, content) => {
    return axios({
        method: 'post',
        url: 'http://' + baseURL + ':5432/posts/editPost',
        headers: {
            Authorization: 'Bearer ' + token
        },
        data: {
            postId: postId,
            content: content
        }
    })
}

export {getAllPosts, deletePost, editPost}

export default ProfileService